@import url(https://fonts.googleapis.com/css2?family=Allison&family=Nunito+Sans:wght@300;600;800&display=swap);
*{margin:0;padding:0;box-sizing:border-box}body{font-family:'Nunito Sans', sans-serif;background-color:#fafafa}.container{max-width:1200px;margin:auto}.containerFull{width:100%}.bgLight{background-color:#fafafa}.bgDark{background-color:#202c37}.header{background-color:#fff;width:100%;box-shadow:0px 0px 3px #eeeeee;cursor:pointer;transition:0.5s all ease-in}.header .content{display:-webkit-flex;display:flex;-webkit-justify-content:space-between;justify-content:space-between;-webkit-align-items:center;align-items:center;height:4rem}.header .content a{text-decoration:none;color:#111517}.headerDark{background-color:#2b3945;width:100%;box-shadow:0px 0px 3px #111517;cursor:pointer;transition:0.5s all ease-in}.headerDark .content{display:-webkit-flex;display:flex;-webkit-justify-content:space-between;justify-content:space-between;-webkit-align-items:center;align-items:center;height:4rem;color:White}.headerDark .content a{text-decoration:none;color:white}#search{display:-webkit-flex;display:flex;-webkit-justify-content:space-between;justify-content:space-between;-webkit-align-items:center;align-items:center;height:5rem;transition:0.5s all ease-in;padding-top:20px}#search .country,#search .region{background-color:White;padding:0.625rem 1.25rem;box-shadow:0px 0px 10px #eeeeee;border-radius:5px;width:300px}#search .country input,#search .region input{background-color:none;border:none;outline:0;margin-left:10px;margin-top:-15px}#search .countryDark{background-color:#2b3945;padding:0.625rem 1.25rem;box-shadow:0px 0px 10px #111517;color:white;border-radius:5px;width:300px}#search .countryDark input{border:none;background-color:#2b3945;color:white;outline:0;margin-left:10px;margin-top:-15px}#search .region{background-color:White;padding:0.625rem 1.25rem;box-shadow:0px 0px 10px #eeeeee;border-radius:5px;width:200px}#search .region select{background-color:White;border:none;width:100%;outline:0px}#search .regionDark{background-color:#2b3945;padding:0.625rem 1.25rem;box-shadow:0px 0px 10px #111517;border-radius:5px;width:200px}#search .regionDark select{background-color:#2b3945 !important;color:white;border:none;width:100%;outline:0px}#countries{display:grid;grid-template-columns:repeat(4, 1fr);grid-gap:2.5rem;gap:2.5rem;padding:3.125rem 0rem}#countries a{text-decoration:none;color:#111517}#countries .country{background-color:white;padding:0px 0px 20px 0px;border-radius:7px;box-shadow:0px 0px 10px #eeeeee;transition:0.5s all ease-in}#countries .country img{border-top-left-radius:7px;border-top-right-radius:7px;width:100%;height:12.625rem;object-fit:cover;object-position:center}#countries .country h2{padding:10px}#countries .country p{padding:0px 15px 10px 15px}#countries .countryDark{background-color:#2b3945;padding:0px 0px 20px 0px;border-radius:7px;box-shadow:0px 0px 10px #111517;color:white;transition:0.5s all ease-in}#countries .countryDark img{border-top-left-radius:7px;border-top-right-radius:7px;width:100%;height:12.625rem;object-fit:cover;object-position:center}#countries .countryDark h2{padding:10px}#countries .countryDark p{padding:0px 15px 10px 15px}@media (max-width: 700px){.header,.headerDark{padding:0px 10px}#search{-webkit-flex-direction:column;flex-direction:column;-webkit-align-items:flex-start;align-items:flex-start;height:auto}#search .country,#search .countryDark{width:90%;margin:20px auto}#search .region,#search .regionDark{width:50%;margin-left:20px}#countries{width:80%;margin:0px auto;padding-top:50px;display:grid;grid-template-columns:repeat(1, 1fr)}}@media (min-width: 600px){#countries{width:80%;margin:0px auto;grid-template-columns:repeat(2, 1fr)}}@media (min-width: 900px){#countries{width:90%;grid-template-columns:repeat(3, 1fr)}}@media (min-width: 1100px){#countries{width:100%;grid-template-columns:repeat(4, 1fr)}}.border,.borderDark{background-color:#fafafa;box-shadow:0px 0px 3px rgba(0,0,0,0.5);text-transform:capitalize;font-size:12px;padding:2px 10px;margin-right:10px;display:inline-block}.borderDark{background-color:#2b3945;color:white}#country{width:1200px;margin:0 auto;padding:3.125rem 0}#country .back,#country .backDark{box-shadow:0px 0px 3px rgba(0,0,0,0.5);border-radius:5px;padding:5px 30px;display:inline-block;margin-bottom:3.125rem;font-size:0.875rem}#country .back a,#country .backDark a{color:#202c37;text-decoration:none}#country .backDark{background-color:#2b3945}#country .backDark a{color:white}#country .details{display:grid;grid-template-columns:repeat(2, 1fr);grid-gap:60px;gap:60px}#country .details .flag img{width:100%;height:400px;object-fit:cover}#country .details .info,#country .details .infoDark{color:#111517;font-size:14px;height:100%;display:-webkit-flex;display:flex;-webkit-flex-direction:column;flex-direction:column;-webkit-justify-content:center;justify-content:center}#country .details .info h2,#country .details .infoDark h2{margin-bottom:20px}#country .details .info .info_details,#country .details .infoDark .info_details{display:-webkit-flex;display:flex;-webkit-justify-content:space-between;justify-content:space-between}#country .details .info .info_details div,#country .details .infoDark .info_details div{padding:0}#country .details .info .info_details div p,#country .details .infoDark .info_details div p{padding-bottom:10px}#country .details .info .info_details div:first-child,#country .details .infoDark .info_details div:first-child{padding-right:40px}#country .details .infoDark{color:White}@media (max-width: 1000px){#country{width:90%;margin:30px auto}#country .details{grid-template-columns:repeat(1, 1fr)}#country .details h2{margin-top:-30px}#country .details .flag{height:auto}#country .details .info_details{-webkit-flex-direction:column;flex-direction:column}#country .details .info_details div:nth-child(2){margin:25px 0px}}

